import React from 'react';
import PhonePanel from '../../components/PhonePanel/phonePanel';
import addReactComponentToRoot from '../../utils/react.utils';
export const addSavePhonePanel = (appProps) => {
    addReactComponentToRoot(<PhonePanel title="Compra rápida e fácil com Nuvem Pay" subtitle="Salve seu celular para agilizar suas próximas compras com o código de segurança na palma da sua mão!" buttonTitle="Salvar na" logTag="SavePhone" data-testid="savePhonePanel"/>, 'optInSuccess', appProps);
};
export const addUpdatePhonePanel = (appProps) => {
    addReactComponentToRoot(<PhonePanel title="Confirme seu celular na Nuvem Pay" subtitle="Receba o código de segurança por SMS e agilize ainda mais suas próximas compras!" buttonTitle="Confirmar na" logTag="UpdatePhone" data-testid="updatePhonePanel"/>, 'optInSuccess', appProps);
};
